.HexButton {
  display: inline-block;
  text-transform: uppercase;
	text-decoration: none;
  font-size: 10px;
	font-weight: bold;
	letter-spacing: 1px;
  border-radius: 50px;
  padding: 10px 20px;
	border: 2px solid white;
	color: #fff;
  transition: 350ms all cubic-bezier(0.190,  1.000, 0.220, 1.000);
  transform: scale(1);
  &.--white{
    border: 2px solid #fff;
    color: white;
    &:hover{
      background-color: rgba(#2D245B,0.5);
    }
  }
  &:hover{
    transform: scale(0.98);
    cursor: pointer;
  }
  &.--full-white{
    border: 2px solid #fff;
    background-color: #fff;
    color: $ovr-purple;
  }
}

