.HexButton.--purple {
	background-image: linear-gradient(90deg,rgba(163, 84, 177,1), rgba(#1d0333,1));
	border: 0px;
}

.HexButton.--blue {
	background-image: linear-gradient(138deg, #022136, #0081DD);
	border: 0px;
}
.HexButton.--orange {
	background-image: linear-gradient(90deg,$ovr-orange,$ovr-yellow);
	border: 0px;
}
.HexButton.--hollow-orange{
	background: linear-gradient(to right, $ovr-orange,$ovr-yellow);
	background-clip: padding-box; /* !importanté */
	border: solid 2px transparent; /* !importanté */
	box-sizing: border-box;
	color: $ovr-orange;
	&:before {
			content: '';
			position: absolute;
			top: 0; right: 0; bottom: 0; left: 0;
			z-index: -1;
			margin: 2px;
			border-radius: inherit;
			background: #fff;
	}
}
