.Map {
	width: 100%;
	height: 350px;
	background: $ovr-aqua-marine;
	&:focus {
		outline: none;
	}
	// margin-top: 50px;
	position: relative;
	box-shadow: -10px 10px 20px 0 rgba(30, 30, 30, 0.03);
}

#js-map-view {
	margin: 0 auto;
	color: #fff;
	background-color: #9c9fa0;
	font-size: 11px;
	padding: 0.2em;
	border: 1px solid #9c9fa0;
	border-radius: 20px;
	/* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2); */
	cursor: pointer;
	position: absolute;
	left: 0.7em;
	top: 10px;
	padding: 2px 10px;
	height: 21px;
	z-index: 9;
}
#js-map-view.on {
	background-position: bottom;
}

#js-map-view:hover {
	background-color: #ccc;
}

.mapboxgl-ctrl-geocoder {
	box-shadow: 0 0 0px rgba(0, 0, 0, 0.1) !important;
}
