
.BuyOfferTile{
  padding: 15px 50px;
  background: linear-gradient(138deg, #022136, #0081DD);
  
  margin-bottom: 10px;
  display: inline-block;
  width: 100%;

  .section{
    margin-left: 10%;
    float: left;
    &:first-child{
      margin-left: 0px;
    }
  }
  
  .ValueCounter{
    position: relative;
    // font-weight: 400;
    .ValueCounter__icon, .ValueCounter__value{
      z-index: 1;
      position: relative;
      color: #fff;
      
    }
  }
  .TimeCounter, b{
    color: #fff;
  }
}

.blinking-message {
    animation: 1s blink infinite 1550ms;
}

.c-small-tile-text{
  color: #fff;
  letter-spacing: 1px;
  outline: none;
}

.BuyOfferModal{
  position: absolute;
  width: 400px;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
  padding: 20px 50px;
  background: linear-gradient(138deg, #022136, #0081DD);
  color: #fff;
  &:focus{
    outline: none;
  }
}

